body {
  font-family: Arial, Helvetica, sans-serif;
}

.navbar-v1 {
  overflow: hidden;
  background-color: #222222;
  width: 100%;
  margin-bottom: 20px;
}

.navbar-v1 a {
  float: left;
  font-size: 16px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown-v1 {
  float: left;
  overflow: hidden;
}

.dropdown-v1 .dropbtn-v1 {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.navbar-v1 a:hover,
.dropdown-v1:hover .dropbtn-v1 {
  background-color: red;
}

.dropdown-content-v1 {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content-v1 a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content-v1 a:hover {
  background-color: #ddd;
}

.dropdown-v1:hover .dropdown-content-v1 {
  display: block;
}
